.login__input {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 300px;
    height: 40px;
    outline: none;
    padding: 0 10px;
}

.login__input--error {
    border-color: red;
}

.error__text {
    color: red;
    min-width: 100px;
    max-width: 300px;
    height: 35px;
    border-radius: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-left: 15px; 
    left: 50%;
    transform: translateX(-50%);
    top :10px
}