.window {
    background-color: #ffffff;
    position: fixed;
    width: 100%;
    height: 100%;
  }
  
  .window__inner {
    background-color: #13CE66;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 300px;
    height: 300px;
    box-shadow: 10px 10px 10px black;
    display: flex;    
    justify-content: center;
    align-items: center;
  }
  
  .window__inner-btn {
    position: fixed;
    right: 10px;
    top: 10px;
    width: 40px;
    height: 40px;
    border: none;
    background: transparent;
    z-index: 0;
  }
  
  .window__inner-btn::after {
    content: '';
    width: 20px;
    height: 2px;
    background-color: black;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%) rotate(45deg);
  }
  
  .window__inner-btn::before {
    content: '';
    width: 20px;
    height: 2px;
    background-color: black;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%) rotate(-45deg);
  }