.sitebar {
  background-color: #13ce66;
  border-radius: 20px;
  grid-area: sitebar;
  height: 800px;
}

.menu__list {
  padding: 0 40px;
}

.menu__list-item {
  padding: 5px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu__list-link {
  font-size: 25px;
  font-weight: 600;
  color: #fff;
}

.menu__list-link:hover {
  color: #ffc82c;
}

.sitebar__settings {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}

.sitebar__settings-link {
  font-size: 25px;
  font-weight: 600;
  color: #fff;
}

.sitebar__settings-link:hover {
  color: #ffc82c;
}

.activeLink {
  color: #ffc82c;
}

a.activeLink {
  color: #ffc82c;
}

.sitebar__friend {
  display: flex; 
  width: 100%;
  justify-content: space-between;
  margin-top: 40px;
  padding: 0 10px;
}

.sitebar__user {
  margin-top: 20px;
  text-align: center;
}