.login {
    padding: 20px 0;
}

.login__title {
    text-align: center;
}

.login__text {
    margin-top: 80px;
    text-align: center;
}

.login__input {
    width: 300px;
    height: 40px;
    padding: 0 10px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    outline: none;
}

.login__btn {
    width: 300px;
    height: 40px;
    padding: 0 10px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    outline: none;
    margin-top: 80px;
    border-radius: 40px;
    border: 1px solid #13CE66;
    background-color: #13CE663b;
}

.login__btn-link {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #13CE66;
}