* {
  box-sizing: border-box;
}

.grid {
  max-width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 2fr 10fr ;
  grid-template-rows: 100px 800px;
  margin-top: 0px;
  grid-template-areas:
  "header header header"
  "sitebar content content";
  gap: 40px;
}

a {
  display: inline-block;
  text-decoration: none;
}

.wrapper__content {
  border-radius: 20px;
  grid-area: content;
  overflow: scroll;
  overflow-x: hidden;
  padding: 10px 0;
  width: 100%;
  position: relative;
}

