.header {
  background-color: #13CE66;
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-area: header;
  border-radius: 0px;
  position: relative;
}

.header__logo-img {
  width: 50px;
  height: 50px;
}

.header__link {
  color: black;
  font-size: 25px;
}

.header__btn {
  background: transparent;
  position: absolute;
  right: 200px;
  width: 60px;
  height: 60px;
  border: none;
}

.header__btn-img {
  width: 100%;
  height: 100%;
}